.seeMore {
  display: flex;
  align-items: baseline;
  gap: 20px;
  padding: 0 30px;
}

.list {
  display: flex;
  gap: 20px;
  margin-top: 8px;
  margin-bottom: 24px;
}

.link {
  font-weight: 700;
  color: #4400ff;
}

.link:not(:last-of-type) {
  margin-bottom: 8px;
}

.link:hover {
  scale: 110%;
  transform-origin: left;
  color: var(--logo-color);
}
